import document from 'global/document';

export default function fireCustomEvent(eventName, data = null) {
  fireCustomEventOnElement(document, eventName, data);
}

export function fireCustomEventOnElement(element, eventName, data = null) {
  let event = null;
  polyfillCustomEvent();

  if (data) {
    event = new CustomEvent(eventName, {
      detail: data,
    });
  } else {
    event = document.createEvent('Event');
    event.initEvent(eventName, true, false);
  }

  element.dispatchEvent(event);
}

function polyfillCustomEvent() {
  if (typeof window.CustomEvent === 'function') return false;

  const CustomEvent = (event, params) => {
    const newParams = params || { bubbles: false, cancelable: false, detail: undefined };
    const evt = document.createEvent('CustomEvent');
    evt.initCustomEvent(event, newParams.bubbles, newParams.cancelable, newParams.detail);
    return evt;
  };

  CustomEvent.prototype = window.Event.prototype;
  window.CustomEvent = CustomEvent;
  return null;
}



